@import '../../../Containers/Variables.scss';

.page-info {
  text-align: justify;
  padding: 20px 40px;
  border-radius: 10px;
  color: $text-color;
  cursor: pointer;
  transition: 0.4s;
  
  &:hover {
    background: darken($background-color, 10%);
  }
}


.band-link {
  // text-decoration: none;
  font-weight: 700;
  transition: 0.4s;
  color: $text-color;
  &:hover {
    color: lighten($text-color, 20%);
    // font-size: 1.1em;
  }
  
}

@media (max-width: $phone-breakpoint) {
  .page-info {
    padding: 10px;
  }
}