@import '../../Containers/Helpers.scss';
@import '../../Containers/Variables.scss';

.page-info-card {
  position: relative;
  padding-bottom: 20px;
  
}

.info-container {
  background: $background-color;
  text-align: justify;
  color: #333;
  
  border-radius: 10px;
  // box-shadow: 0px 4px 10px darken(#999999, 30%);
  color: $text-color;
  cursor: pointer;
  transition: 0.4s;
  margin-top: -20px;
  h2 {
    font-size: 26px;
    margin-bottom: 20px;
  }

  p {
    font-size: 20px;
    margin-bottom: 20px;
    line-height: 1.5;
  }
  span {
    // display: inline-block;
    font-weight: 800;
    // text-align: right;
  }
}

.darkened-background {
  background: darken($background-color, 10%);
}

@media (max-width: $phone-breakpoint) {
  .page-info-card {
    h2 {
      font-size: 15px;
    }

    p {
      font-size: 12px;
    }
  }
}