@import '../../../Containers/Variables.scss';

.offer {
    text-align: justify;
    color: $text-color;
    cursor: pointer;
    border-radius: 10px;
    padding: 20px 40px;
    transition: 0.4s;
    &:hover {
      background: darken($background-color, 10%);
    }
  }

  
@media (max-width: $phone-breakpoint) {
  .offer {
    padding: 10px;
  }
}