.page-info-flex-container {
  display: flex;
  .page-info-text {
    width: 50%;

  }
  .page-info-images {
    width: 50%;
    padding-left: 20px;
    img {
      width: 100%;
      border-radius: 10px;
      box-shadow: 0px 0px 20px #555;
    }
  }
}

.info-video {
  box-shadow: 0px 0px 20px #555;
  border-radius: 10px;
  overflow: hidden;
}
