@import '../../Containers/Helpers.scss';
@import '../../Containers/Variables.scss';

.page-videos {
  display: flex;
  position: relative;
  padding-bottom: 20px;
  .video {
    width: 50%;
  }
}

@media (max-width: $container-width) {
  .page-videos {
    display: block;
    .video {
      width: 100%;
    }
  }
}
