@import '../../Containers/Variables.scss';
@import '../../Containers/Helpers.scss';

.page-gallery {
  // margin-top: 100px;
  transition: 0.4s;
  position: relative;
  padding-bottom: 20px;
  button {
    outline: none;
    border: none;
    background: url('./Images/button/gallery-button.jpg') no-repeat;
    background-size: cover;
    background-position: center center;
    width: 100%;
    height: 600px;
    transition: 0.4s;
    font-size: 40px;
    cursor: pointer;
    
    color: #fff;
    text-shadow: 0 2px 2px #666;
    &:hover .gallery-button-darken {
      background: rgba(#000, 0.4);
    }

    &:hover .gallery-mock-button {
      // background: darken(darkcyan, 20%);
      width: 320px;
      height: 320px;
      font-size: 45px;
      
    }
  }

  .gallery-button-darken {
    height: 100%;
    width: 100%;
    transition: 0.4s;
  }
  .gallery-mock-button {
    width: 300px;
    height: 300px; 
    background: url('../Header/Images/treblers-icon-large.png') no-repeat;
    background-size: contain;
    border-radius: 6px;
    margin: 0 auto;
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0; 
    transition: 0.4s;
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      // padding: 0;
      // text-shadow: none;
      // background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #9f7928 30%, #8A6E2F 40%, transparent 80%),
      // radial-gradient(ellipse farthest-corner at left top, #FFFFFF 0%, #FFFFAC 8%, #D1B464 25%, #5d4a1f 62.5%, #5d4a1f 100%);
      // background-clip: text;
      // color: transparent;
      
    }
  }
  img {
    width: 500px;
    // height: 400px;
  }

  .viewer {
    transition: 0.4s;
  }

  .page-gallery-darkened-background {
    width: 100%;
    position: fixed;
    top: 0;
    height: 100vh;
    z-index: 2000;
    background: rgba(#000, 0.8);
    transition: 0.6s;
  }
  

  
  .page-gallery-thumbails {
    justify-content: center;
    margin-top: 30px;
    display: grid;
    grid-gap: 5%;
    grid-template-columns: repeat(auto-fill, 20%);
    align-items: center;
    height: 90vh;
    overflow: auto;

  }
  .gallery-thumbnail {
    width: 100%;
    
  }

  @media (max-width: $tablet-breakpoint) {
    
    .page-gallery-thumbails {
      grid-gap: 3%;
      grid-template-columns: repeat(auto-fill, 30%);
    }
  
  
  }

  @media (max-width: $phone-breakpoint) {
    .page-gallery-thumbails {
      grid-gap: 3%;
      grid-template-columns: repeat(auto-fill, 45%);
    }
  }
}

@media (max-width: $container-width) {
  .page-gallery {
    
    width: 100%;
    button {
      height: 300px;
      
      background-size: contain;

      &:hover .gallery-mock-button {
        // background: darken(darkcyan, 20%);
        width: 106px;
        height: 106px;
        font-size: 22px;
        
      }
    }

    .gallery-mock-button {
      height: 100px;
      width: 100px;
      span {
        font-size: 20px;
      }
    }
  }
}
  
   
