@import '../../Containers/Helpers.scss';
@import '../../Containers/Variables.scss';

.sticky {
  position: fixed;
  top: 0;
}

.page-header {
  background: url('./Images/Backgrounds/blue-smoke.jpg') no-repeat;
  background-position: center;
 
  width: 100%;
  z-index: 1000;
  
}

.page-header-internal {
  display: flex;
  justify-content: space-between;
}

.mock-header {
  height: 100px;
}

@media (max-width: $phone-breakpoint) {
  .mock-header {
    height: 50px;
  }
}