@import '../../../Containers/Variables.scss';

.menu {
  height: 100%;
  transition: 0.5s;
 
  ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      align-items: center;
      list-style: none;
      height: 100px;

  }
  .menu-item-wrapper {
    width: 100%;
  }

}

@media (max-width: 1000px) {
  .menu {
    height: 360px;
    position: absolute;
    top: 100px;
    right: -400px;
    // z-index: 100;
    background: url(../Images/Backgrounds/blue-smoke.jpg);
    background-position: center center;
    // border: 1px solid darken($background-color, 50%);
    border-bottom-left-radius: 10px;
    // padding-right: 40px;

    

    &.menu-show {
      right: 0;
      .line-top {
        transform: translate(-10px, 17px) rotate(225deg);
        width: 118%;
      }

      .line-bottom {
        transform: translate(-10px, -17px) rotate(-225deg);
        width: 118%;
      }

      .line-middle {
        transform: translate(200px, 0);
        background: transparent;
      }
    }

    .hamburger-icon {
      width: 60px;
      height: 60px;
      // background: #333;
      position: fixed;
      top: 20px;
      right: 20px;

      .hamburger-line {
        width: 100%;
        height: 2px;
        background: darken($background-color, 40%);
        margin: 15px 0;
        transition: 0.5s;

      }

      
    }
    ul {
        // height: 500px;
        display: block;
        // background: yellow;
        // display: flex;
        // flex-direction: column;
        justify-content: space-between;
        align-items: center;
        align-items: center;
        list-style: none;
        height: auto;
        width: 100%;
        
        
    }
}

}

@media (max-width: $phone-breakpoint) {
  .menu {
    top: 70px;
    height: 250px;

    &.menu-show {
      right: 0;
      .line-top {
        transform: translate(-5px, 8px) rotate(225deg);
        width: 118%;
      }

      .line-bottom {
        transform: translate(-5px, -9px) rotate(-225deg);
        width: 118%;
      }

      .line-middle {
        transform: translate(200px, 0);
        background: transparent;
      }
    }
    .hamburger-icon {
      width: 30px;
      height: 30px;
      // background: #333;
      position: fixed;
      top: 20px;
      right: 10px;
      .hamburger-line {
        margin: 7px 0;
      }
    }

  }
}
