@import '../../Containers/Helpers.scss';
@import '../../Containers/Variables.scss';

.page-footer {
  background: $background-color;
  // height: 100px;
  padding: 30px 0;
  color: $text-color;
  .container {
    display: flex;
    justify-content: space-between;
  }
  font-size: 20px;

  .footer-email {
    display: flex;
    align-items: center;
    span {
      font-size: 20px;
    }

    a {
      display: flex;
      align-items: center;
    }
  }

  .footer-icons {
    
  }
  
  span {
    font-weight: 700;
  }
  a {
    // font-size: 26px;
    text-decoration: none;
    color: $text-color;
    display: inline-block;
    padding: 0 20px;
    transition: 0.4s;
    &:hover {
      color: #fff;
    }
  }
}